import React from "react";
import "../css/slide.css";
const Slide = () => {
  return (
    <div className="my-slide">
      <div className="">
        <div
          id="carouselExampleControls"
          className="carousel slide"
          data-bs-ride=""
        >
          <div className="carousel-inner">
            <div className="carousel-item active">
              <div className="my-slide-cards font-size-18">
                <div className="my-card">
                  <div className="title">
                    <h3>
                      HELLO WORLD! <br /> WE ARE KASPER,WE MAKE ART.
                    </h3>
                  </div>
                  <div className="content">
                    Curabitur arcu erat, accumsan id imperdiet et, porttitor at
                    sem. Mauris blandit aliquet elit, eget tincidunt nibh
                    pulvinar a. Curabitur aliquet quam. Accumsan id imperdiet
                    et, porttitor at sem. Mauris blandit aliquet elit, eget
                    tincidunt.
                  </div>
                </div>
              </div>
            </div>
            <div className="carousel-item">
              <div className="my-slide-cards font-size-18">
                <div className="my-card">
                  <div className="title">
                    <h3>
                      HELLO WORLD! <br /> WE ARE KASPER,WE MAKE ART.
                    </h3>
                  </div>
                  <div className="content">
                    Curabitur arcu erat, accumsan id imperdiet et, porttitor at
                    sem. Mauris blandit aliquet elit, eget tincidunt nibh
                    pulvinar a. Curabitur aliquet quam. Accumsan id imperdiet
                    et, porttitor at sem. Mauris blandit aliquet elit, eget
                    tincidunt.
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div>
            <button
              className="carousel-control-prev"
              type="button"
              data-bs-target="#carouselExampleControls"
              data-bs-slide="next"
            >
              <span
                className="carousel-control-prev-icon"
                aria-hidden="true"
              ></span>
              <span className="visually-hidden">Previous</span>
            </button>

            <button
              className="carousel-control-next"
              type="button"
              data-bs-target="#carouselExampleControls"
              data-bs-slide="prev"
            >
              <span
                className="carousel-control-next-icon"
                aria-hidden="true"
              ></span>
              <span className="visually-hidden">Next</span>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Slide;
