import React, { useEffect } from "react";
import SectionTitle from "./layouts/SectionTitle";
import "../css/portofolio.css";
import img1 from "../imgs/shuffle-01.jpg";
import img2 from "../imgs/shuffle-02.jpg";
import img3 from "../imgs/shuffle-03.jpg";
import img4 from "../imgs/shuffle-04.jpg";
import img5 from "../imgs/shuffle-05.jpg";
import img6 from "../imgs/shuffle-06.jpg";
import img7 from "../imgs/shuffle-07.jpg";
import img8 from "../imgs/shuffle-08.jpg";
import video from "../imgs/awesome-video.mp4";
import Aos from "aos";
import "aos/dist/aos.css";

const Portofolio = () => {
  useEffect(() => {
    Aos.init({ duration: 2000 });
  }, []);
  return (
    <div className="portofolio">
      <SectionTitle header="Portofolio" />
      <div className="row" style={{ margin: "0 " }}>
        <div className="col-lg-3 col-md-6 col-sm-12">
          <div data-aos="flip-left" className="shuffle">
            <img src={img1} alt="" />
            <div class="caption">
              <h4>Awesome Image</h4>
              <p>Photography</p>
            </div>
          </div>
        </div>
        <div className="col-lg-3 col-md-6 col-sm-12">
          <div data-aos="flip-right" className="shuffle">
            <img src={img2} alt="" />
            <div class="caption">
              <h4>Awesome Image</h4>
              <p>Photography</p>
            </div>
          </div>
        </div>
        <div className="col-lg-3 col-md-6 col-sm-12">
          <div data-aos="flip-up" className="shuffle">
            <img src={img3} alt="" />
            <div class="caption">
              <h4>Awesome Image</h4>
              <p>Photography</p>
            </div>
          </div>
        </div>
        <div className="col-lg-3 col-md-6 col-sm-12">
          <div data-aos="flip-down" className="shuffle">
            <img src={img4} alt="" />
            <div class="caption">
              <h4>Awesome Image</h4>
              <p>Photography</p>
            </div>
          </div>
        </div>
      </div>
      <div className="row" style={{ margin: "0 " }}>
        <div className="col-lg-3 col-md-6 col-sm-12">
          <div data-aos="fade-up-right" className="shuffle">
            <img src={img5} alt="" />
            <div class="caption">
              <h4>Awesome Image</h4>
              <p>Photography</p>
            </div>
          </div>
        </div>
        <div className="col-lg-3 col-md-6 col-sm-12">
          <div data-aos="zoom-in" className="shuffle">
            <img src={img6} alt="" />
            <div class="caption">
              <h4>Awesome Image</h4>
              <p>Photography</p>
            </div>
          </div>
        </div>
        <div className="col-lg-3 col-md-6 col-sm-12">
          <div data-aos="zoom-out" className="shuffle">
            <img src={img7} alt="" />
            <div class="caption">
              <h4>Awesome Image</h4>
              <p>Photography</p>
            </div>
          </div>
        </div>
        <div className="col-lg-3 col-md-6 col-sm-12">
          <div data-aos="fade-up-left" className="shuffle">
            <img src={img8} alt="" />
            <div class="caption">
              <h4>Awesome Image</h4>
              <p>Photography</p>
            </div>
          </div>
        </div>
      </div>
      <div className="video">
        <video autoPlay muted loop>
          <source src={video} type="video/mp4" />
        </video>
        <div className="text">
          <h2>Super Awesome Video Here</h2>
          <p>Its All You Need</p>
        </div>
      </div>
    </div>
  );
};

export default Portofolio;
