import React from "react";
import Skills from "./Skills";
import Testimonials from "./Testimonials";
import "../css/skills_testi.css";

const Skills_Testi = () => {
  return (
    <div className="skills-testimonials">
      <div className="container">
        <div className="row">
          <div className="col-lg-6 col-md-12 col-sm-12">
            <Testimonials />
          </div>
          <div className="col-lg-6 col-md-12 col-sm-12">
            <Skills />
          </div>
        </div>
      </div>
      <div class="quote text-center">
        <div class="container">
          <q>
            ACCUMSAN ID CURABITUR ET PORTITOR MAURIS BLANDIT DOLOR LOREM SOME
            EST OUR DONEC STIN LORTIOS
          </q>
          <span>John Doe</span>
        </div>
      </div>
    </div>
  );
};

export default Skills_Testi;
