import React from "react";
import "../css/about.css";
import SectionTitle from "./layouts/SectionTitle";
import about from "../imgs/about.png";
import Skills from "./Skills";
const About = () => {
  return (
    <div className="about">
      <SectionTitle header="About us " />
      <div className="about-img">
        <img src={about} alt="" />
      </div>
      <div className="about-state">
        <div className="container">
          <div className="states">
            <div className="row">
              <div className="col-lg-3 col-md-6 col-sm-12">
                <div className="icon">
                  <i class="fas fa-mug-hot rounded-circle"></i>
                </div>
                <div className="state">
                  <div class="number">1.236</div>
                  <p>Coffee Drinks</p>
                </div>
              </div>
              <div className="col-lg-3 col-md-6 col-sm-12">
                <div className="icon">
                  <i class="far fa-folder rounded-circle"></i>
                </div>
                <div className="state">
                  <div class="number">256</div>
                  <p>Completed Projects</p>
                </div>
              </div>
              <div className="col-lg-3 col-md-6 col-sm-12">
                <div className="icon">
                  <i class="far fa-envelope rounded-circle"></i>
                </div>
                <div className="state">
                  <div class="number">1.743</div>
                  <p>Mail Sent</p>
                </div>
              </div>
              <div className="col-lg-3 col-md-6 col-sm-12">
                <div className="icon">
                  <i class="fas fa-trophy rounded-circle"></i>
                </div>
                <div className="state">
                  <div class="number">17</div>
                  <p>Awards Received</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;
