import React, { useEffect } from "react";
import SectionTitle from "./layouts/SectionTitle";
import Aos from "aos";
import "aos/dist/aos.css";
const Skills = () => {
  useEffect(() => {
    Aos.init({ duration: 3000 });
  }, []);
  return (
    <div className="skills">
      <SectionTitle header="Skills" />
      <div className="prog-holder">
        <h4>Adobe</h4>
        <div data-aos="flip-left" className="prog">
          <span className="adobe" data-progress="90%"></span>
        </div>
      </div>
      <div className="prog-holder">
        <h4>Html &amp; Css</h4>
        <div data-aos="flip-right" className="prog">
          <span className="html" data-progress="85%"></span>
        </div>
      </div>
      <div className="prog-holder">
        <h4>JavaScript</h4>
        <div data-aos="flip-up" className="prog">
          <span className="js" data-progress="80%"></span>
        </div>
      </div>
      <div className="prog-holder">
        <h4>Php</h4>
        <div data-aos="flip-down" className="prog">
          <span className="php" data-progress="90%"></span>
        </div>
      </div>
    </div>
  );
};

export default Skills;
